export enum CURSOR {
    Default = 'default',
    Auto = 'audo',
    Crosshair = 'CROSSHAIR',
    Move = 'move',
    Pointer = 'pointer',
    Text = 'text',
    Wait = 'wait',
    Help = 'help',
    NWResize = 'nw-resize', // 0, 2
    NEResize = 'ne-resize', // 1, 3
    NSResize = 'ns-resize', // 4 6
    EResize = 'e-resize', // 5 7
}
export class ActiveCursor {
    public div: HTMLElement|undefined;
    constructor(div?: HTMLElement) {
        this.div = div;
    }
    public default() {
        if (this.div) {
            if (this.div.style.cursor !== CURSOR.Default) { this.div.style.cursor = CURSOR.Default; }
        } else {
            if (document.body.style.cursor !== CURSOR.Default) { document.body.style.cursor = CURSOR.Default; }
        }
    }
    public auto() {
      if (this.div) {
          if (this.div.style.cursor !== CURSOR.Auto) { this.div.style.cursor = CURSOR.Auto; }
      } else {
          if (document.body.style.cursor !== CURSOR.Auto) { document.body.style.cursor = CURSOR.Auto; }
      }
  }
    public creating() {
        if (this.div) {
            if (this.div.style.cursor !== CURSOR.Crosshair) { this.div.style.cursor = CURSOR.Crosshair; }
        } else {
            if (document.body.style.cursor !== CURSOR.Crosshair) { document.body.style.cursor = CURSOR.Crosshair; }
        }
    }
    public dragging() {
        if (this.div) {
            if (this.div.style.cursor !== CURSOR.Move) { this.div.style.cursor = CURSOR.Move; }
        } else {
            if (document.body.style.cursor !== CURSOR.Move) { document.body.style.cursor = CURSOR.Move; }
        }
    }
    public hovering() {
        if (this.div) {
            if (this.div.style.cursor !== CURSOR.Pointer) { this.div.style.cursor = CURSOR.Pointer; }
        } else {
            if (document.body.style.cursor !== CURSOR.Pointer) { document.body.style.cursor = CURSOR.Pointer; }
        }
    }
    public texting() {
        if (this.div) {
            if (this.div.style.cursor !== CURSOR.Text) { this.div.style.cursor = CURSOR.Text; }
        } else {
            if (document.body.style.cursor !== CURSOR.Text) { document.body.style.cursor = CURSOR.Text; }
        }
    }
    public set_e_resize() {
      if (this.div) {
          if (this.div.style.cursor !== CURSOR.EResize) { this.div.style.cursor = CURSOR.EResize; }
      } else {
          if (document.body.style.cursor !== CURSOR.EResize) { document.body.style.cursor = CURSOR.EResize; }
      }
      }
      public set_ne_resize() {
          if (this.div) {
              if (this.div.style.cursor !== CURSOR.NEResize) { this.div.style.cursor = CURSOR.NEResize; }
          } else {
              if (document.body.style.cursor !== CURSOR.NEResize) { document.body.style.cursor = CURSOR.NEResize; }
          }
      }
      public set_ns_resize() {
          if (this.div) {
              if (this.div.style.cursor !== CURSOR.NSResize) { this.div.style.cursor = CURSOR.NSResize; }
          } else {
              if (document.body.style.cursor !== CURSOR.NSResize) { document.body.style.cursor = CURSOR.NSResize; }
          }
      }
      public set_nw_resize() {
          if (this.div) {
              if (this.div.style.cursor !== CURSOR.NWResize) { this.div.style.cursor = CURSOR.NWResize; }
          } else {
              if (document.body.style.cursor !== CURSOR.NWResize) { document.body.style.cursor = CURSOR.NWResize; }
          }
    }
}