import { Canvas } from './Canvas';
import { Frame } from './Frame';
import { process_image } from '../lib/DominantColor';
import { RGB2HSL, HSL2RGB } from '../lib';
import { Renderer } from './Renderer';
import { ImageWrapper } from './ImageWrapper';
import { EventController } from './EventController';
import { Distort } from './Distort';
import { ExpreeImageTool } from '..';


export class Core {
    public app: ExpreeImageTool;
    public div: HTMLElement;

    public imageWrapper: ImageWrapper;
    public frame: Frame;
    public renderer: Renderer;
    public canvas: Canvas;
    public controller: EventController;
    public ctx: CanvasRenderingContext2D;
    public img: HTMLImageElement;
    public distort: Distort;

    constructor(app: ExpreeImageTool) {
        this.app = app;

        this.initApp();
        this.initCanvas();
        this.importImage('https://raw.githubusercontent.com/NamjuLee/data/master/img/MITBuilding7_small.jpg');


    }
    private initApp() {
        this.frame = new Frame(this);
        this.renderer = new Renderer(this);
        this.controller = new EventController(this);
    }
    private initCanvas() {
        const div = document.createElement('div');
        div.id = 'ExpressImageDivCanvas';
        // div.style.width = '400px';
        // div.style.height = '400px';
        // div.style.margin = '50px';
        div.style.overflow = 'hidden';
        div.style.textAlign = 'center';
        // div.style.backgroundColor = '#ff0000';
        this.div = div;
        this.app.div.append(this.div);

        this.canvas = new Canvas(this, div);
        this.canvas.render = (ctx: CanvasRenderingContext2D) => { this.renderer.render(ctx); };
    }
    public importImage(url: string) {
        const image = new Image();
        image.crossOrigin = 'anonymous';
        image.src = url;
        image.style.pointerEvents = 'none';
        this.renderer.isCapture = false;
        image.onload = () => {
            this.imageWrapper = new ImageWrapper(this, image);

            this.canvas.canvasOut.width = image.width;
            this.canvas.canvasOut.height = image.height;

            this.canvas.canvas.width = image.width;
            this.canvas.canvas.height = image.height;

            // this.div.style.width = image.width + 'px';
            this.div.style.height = image.height + 'px';

            this.img = image;

            console.log(image);
            const result = process_image(image);
            // console.log(result);

            // // deSaturation
            const colResult = [result[0][0], result[0][1], result[0][2]];
            let hls0 = RGB2HSL(colResult[0], colResult[1], colResult[2]);
            let hls1 = [...hls0];


            // console.log(hls);
            hls0[0] += 0.0; // Hue;
            hls0[1] += -0.05; // Saturation;
            if (hls0[1] < 0) { hls0[1] = 0; }
            hls0[2] += 0.0; // Lightness;
            let col0 = HSL2RGB(hls0[0], hls0[1], hls0[2]);
            console.log('col0', col0);
            this.app.div.style.backgroundColor = `rgb(${col0[0]},${col0[1]},${col0[2]})`;

            this.app.ui.updateStyle(col0[0], col0[1], col0[2]);
            this.app.divHost.style.backgroundColor = `rgb(${col0[0]},${col0[1]},${col0[2]})`;

            hls1[0] += 0.0; // Hue;
            hls1[1] += 0.0; // Saturation;
            if (hls1[1] < 0) { hls1[1] = 0; }
            hls1[2] += -0.02; // Lightness;
            let col1 = HSL2RGB(hls1[0], hls1[1], hls1[2]);
            console.log('col1', col1);
            this.frame.updateColorBG(col1[0], col1[1], col1[2]);

            this.frame.InitFrame();


            this.renderer.isCapture = true;
            this.canvas.renderForce();

            this.distort = new Distort(this);
        };
    }
}