import { ProjectionBase } from '../Projection';
import { Distort } from '..';

// reference: http://jsfiddle.net/xzL58dha/3/
// https://stackoverflow.com/questions/4097688/draw-distorted-image-on-html5s-canvas

export class ProjectionSubdiv extends ProjectionBase {
    constructor(distort: Distort) {
        super();
        this.distort = distort;
    }
    public renderDistortion(ctx: CanvasRenderingContext2D, img: HTMLImageElement) {
        const corners: number[][] = [];
        let xSum = 0;
        let ySum = 0;
        for (let i = 0 ; i < this.distort.Controllers.length; ++i) {
            const x = (this.distort.Controllers[i].x - this.distort.domRect.x) * this.distort.domRect.width / this.distort.hostImg.width;
            const y = (this.distort.Controllers[i].y - this.distort.domRect.y) * this.distort.domRect.height / this.distort.hostImg.height;
            xSum += x;
            ySum += y;
            corners.push([x, y]);
        }
        corners.push([xSum / this.distort.Controllers.length, ySum / this.distort.Controllers.length]);

        // 0   1
        //   4
        // 3   2
        const w = this.distort.hosCanvas.width;
        const h = this.distort.hosCanvas.height;
        // drawTriangle(ctx, img, [0, 0], [w / 2, h / 2], [0, h],  corners[0], corners[4], corners[3]);
        // drawTriangle(ctx, img, [0, 0], [w / 2, h / 2], [w, 0],  corners[0], corners[4], corners[1]);
        // drawTriangle(ctx, img, [w, 0], [w / 2, h / 2], [w, h],  corners[1], corners[4], corners[2]);
        // drawTriangle(ctx, img, [0, h], [w / 2, h / 2], [w, h],  corners[3], corners[4], corners[2]);
    }
}


