import { Core }from './Core';
import { UI } from './UI';

export interface IImageEditorParam {
    id: string;
    ui?: boolean;
    colorCover?: string;
}
export class ExpreeImageTool {

    public core: Core;
    public ui: UI;
    public divHost: HTMLElement;
    public div: HTMLElement;
    public param: IImageEditorParam;

    constructor(param: IImageEditorParam) {
        this.param = param;
        const divHost = document.getElementById(this.param.id);
        const div = document.createElement('div');
        if (divHost && div) {
            this.div = div;
            this.div.id = 'ImageDistortion';
            this.div.style.padding = '50px';

            this.divHost = divHost;
            this.divHost.append(this.div);
            // this.divHost.style.padding = '50px';

            if (this.param.ui === undefined) {
                this.param.ui = true;
            }

            if (this.param.ui) {
                // console.log(this.param.ui);
                this.ui = new UI(this);
                this.ui.initPost();
            }

            this.core = new Core(this);
            // template for the style of canvas
            // this.core.canvs.div.style.margin = '50px';
            // this.core.canvs.div.style.padding = '50px';

        }
    }
    public Dummy() {
        //
    }
}
