import { Core } from '..';
export class Renderer {
    public core: Core;
    public t = 0.0;
    public isCapture = false;
    constructor(core: Core) {
        this.core = core;
    }
    public render(ctx: CanvasRenderingContext2D) {

        // this.translation[0] = this.rect.cv[0];
        // this.translation[1] = this.rect.cv[1];

        // console.log(this.translation);
        // this.ctx.translate(this.translation[0], this.translation[1]);
        // this.ctx.scale(this.zoom, this.zoom);
        // this.ctx.rotate(this.rotation)
        // this.ctx.translate(this.rect.cv[0], this.rect.cv[1]);
        // this.ctx.translate(100, 100);

        // this.Render(ctx);
        if (this.core.img) {
            ctx.save();
            
            let type = 1;
            switch (type) {
                case 0:
                    const mat = new DOMMatrix([1, 0, 0, 1, 0, 0]);

                    const xPos = this.core.img.width; // * .5;
                    const yPos = this.core.img.height; // * .5;

                    const xPosRatio = this.core.frame.cv[0] / this.core.img.width; // * 0.01;
                    const yPosRatio = this.core.frame.cv[1] / this.core.img.height; // * 0.01;

                    console.log(xPosRatio, yPosRatio)
                    console.log(1 - xPosRatio, 1 - yPosRatio)

                    ctx.setTransform(mat
                        .translate(xPos * xPosRatio, yPos * yPosRatio)
                        .rotate(this.core.controller.rotationRadian)
                        .scale(this.core.controller.zoomLevel)
                        .translate(xPos * (1 - xPosRatio ), yPos * (1 - yPosRatio))
                    );
                    ctx.drawImage(this.core.img, -this.core.img.width, -this.core.img.height);
                    break;

                case 1:
                    ctx.translate(this.core.img.width * 0.5, this.core.img.height * 0.5);
                    ctx.rotate(this.core.controller.rotationRadian);
                    ctx.scale(this.core.controller.zoomLevel, this.core.controller.zoomLevel);
                    ctx.drawImage(this.core.img, -this.core.img.width * 0.5, -this.core.img.height * 0.5);
                    break;
                default:
                    break;
            }




            // ctx.beginPath();
            // ctx.arc(0, 0, 10,  0, 3.14 * 2);
            // ctx.closePath()
            // ctx.fill();

            ctx.restore();
        }

        this.core.canvas.clear(ctx);
        this.core.distort?.render(ctx);

        this.captureImage();


        // this.ctx.restore();
        // this.ctx.save();

        // this.ctx.translate(this.translation[0], this.translation[1]);
        this.core.frame.render(ctx);
 




        let op = 1 - this.t;
        // console.log(op);

        if (op <  0) {
            op = 0;
        }


        ctx.beginPath();
        const xx = this.core.controller.touchInteraction.touchPre[0];
        const yy = this.core.controller.touchInteraction.touchPre[1];
        ctx.arc(xx, yy, op * 10,  0, 3.14 * 2);
        ctx.closePath();


        // console.log(`rgba(255, 255, 255, ${op})`)

        ctx.fillStyle = `rgba(255, 255, 255, ${op})`;
        ctx.fill();


        ctx.restore();

        // this.ctx.restore();
        this.t += 0.025;
    }
    private captureImage() {

        if (this.core.controller.isDrag) {
            this.updateCanvasCapture();
        }

        if (this.isCapture) {
            // console.log(this.core.frame.x);
            this.core.canvas.ctxOut.drawImage(this.core.canvas.canvas, -this.core.frame.x, -this.core.frame.y);
        }
        // console.log(this.core.canvs.ctxOut, this.core.canvs);
    }
    public updateCanvasCapture() {
            // this.core.frame.cv[0] = this.core.frame.x + this.core.frame.width * 0.5;
            // this.core.frame.cv[1] = this.core.frame.y + this.core.frame.height * 0.5;

            this.core.canvas.canvasOut.width = this.core.frame.width;
            this.core.canvas.canvasOut.height = this.core.frame.height;
    }
}