// import { renderVertex } from '../../../lib';
import { linearColorInterpolation } from '../../../lib';
import { Frame } from '..';

export class Renderer {
    public frame: Frame;

    private _opacity = 0.0;
    private _t = 0.0;

    private _strokeStyle = 'rgba(200, 200, 200, 1)';
    private _fillStyleRuntime = '';

    private _fillStyle = [200, 200, 200, 1.0];
    private _fillStyleHover = [200, 200, 200, 0.5];

    private _sizePoint: number = 3;
    private _sizeMidPoint: number = 3;

    constructor(frame: Frame) {
        this.frame = frame;
    }
    get sizePoint() {
        return this._sizePoint;
    }
    get sizeMidPoint() {
        return this._sizeMidPoint;
    }
    public updateColorBG(r: number, g: number, b: number) {
        console.log(r, g, b);
        this._fillStyle[0] = r;
        this._fillStyle[1] = g;
        this._fillStyle[2] = b;

        this._fillStyleHover[0] = r;
        this._fillStyleHover[1] = g;
        this._fillStyleHover[2] = b;
    }
    public render(ctx: CanvasRenderingContext2D) {
        // ctx.globalCompositeOperation = 'xor';
        this.ColorInterpolation();
        if (this.frame.isHover) {
            ctx.fillStyle = this._fillStyleRuntime; // this.fillStyleHover;
        } else {
            ctx.fillStyle = this._fillStyleRuntime; // this.fillStyle;
        }

        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, this.frame.y);
        ctx.rect(0, this.frame.y + this.frame.height, ctx.canvas.width, ctx.canvas.height);
        ctx.rect(0, 0, this.frame.x, ctx.canvas.height);
        ctx.rect(this.frame.x + this.frame.width , 0, ctx.canvas.width, ctx.canvas.height);
        ctx.fill();

        this.renderPost(ctx);
        this._t += 0.15;
    }
    public renderPost(ctx: CanvasRenderingContext2D) {
        // debuger
        // ctx.beginPath();
        // ctx.arc(0, 0, 15, 0, 3.14 * 2);
        // ctx.closePath()
        // ctx.fillStyle = '#ff0000';
        // ctx.fill();

        //
        ctx.lineWidth = 1;
        ctx.lineDashOffset = this._t;
        ctx.setLineDash([4, 1]);
        ctx.beginPath();
        ctx.rect(this.frame.x, this.frame.y, this.frame.width, this.frame.height);
        ctx.strokeStyle = this._strokeStyle;
        ctx.stroke();

        // guides
        ctx.beginPath();
        for (let i = 1 ; i < 3; ++i) {
            ctx.moveTo(this.frame.x + this.frame.xOffGuid * i, this.frame.y);
            ctx.lineTo(this.frame.x + this.frame.xOffGuid * i, this.frame.y + this.frame.height);
            ctx.moveTo(this.frame.x, this.frame.y + this.frame.yOffGuid * i);
            ctx.lineTo(this.frame.x + this.frame.width , this.frame.y + this.frame.yOffGuid * i);
        }
        ctx.stroke();
        ctx.setLineDash([]);

        // render the back circlr of selected point
        if (this.frame.idHover !== -1) {
            ctx.beginPath();
            let p = [0, 0];

            p[0] = this.frame.cpList[this.frame.idHover].x;
            p[1] = this.frame.cpList[this.frame.idHover].y;

            ctx.arc(p[0], p[1], 8 + 2 * Math.sin(this._t * 0.75), 0, 3.14 * 2);
            ctx.closePath();
            ctx.fillStyle = 'rgba(255, 255, 255, 0.5)';
            ctx.fill();
        }
        // end render the back circlr of selected point

        // render each control point of the frame
        if (this.frame.isHover) {
            for (let i = 0 ; i < this.frame.cpList.length; ++i) {
                this.frame.cpList[i].render(ctx);
            }
        }
        // end render each control point of the frame

    }
    private ColorInterpolation() {
        // let opacity = 0;
        if (this.frame.isHover) {
            this._opacity += 0.05; // this.tOpacityA;
        } else {
            this._opacity -= 0.035; // this.tOpacityB;
        }

        if (this._opacity > 1) {
            this._opacity = 1.0;
        }
        if (this._opacity < 0) {
            this._opacity = 0.0;
        }

        const col = linearColorInterpolation(this._fillStyle, this._fillStyleHover, this._opacity);
        this._fillStyleRuntime = `rgba(${col[0]}, ${col[1]}, ${col[2]}, ${col[3]})`;
    }
}