
import { renderVertex, distance } from '../../../lib';
import { Frame } from '..';

export enum CP_TYPE {
    Endpoint = 'Endpoint',
    Midpoint = 'Midpoint'
}
export class ControlPoint {
    public x: number;
    public y: number;
    public frame: Frame;
    public id: number;
    public type: CP_TYPE;
    constructor(frame: Frame, id: number, type: CP_TYPE) {
        this.frame = frame;
        this.id = id;
        this.type = type;
        this.update();
    }
    public isHoverCheck(x: number, y: number) {
        if (distance(this.x, this.y, x, y) < this.frame.r) {
            return this.id;
        }
        return -1;
    }
    public update() {
        if (this.type === CP_TYPE.Endpoint) {
            if (this.id === 0) {
                this.x = this.frame.x;
                this.y = this.frame.y;
            } else if (this.id === 1) {
                this.x = this.frame.x + this.frame.width;
                this.y = this.frame.y;
            } else if (this.id === 2) {
                this.x = this.frame.x + this.frame.width;
                this.y = this.frame.y + this.frame.height;
            } else if (this.id === 3) {
                this.x = this.frame.x;
                this.y = this.frame.y + this.frame.height;
            }
        } else if (this.type === CP_TYPE.Midpoint) {
            if (this.id === 4) {
                this.x = this.frame.x + this.frame.width * 0.5;
                this.y = this.frame.y;
            } else if (this.id === 5) {
                this.x = this.frame.x + this.frame.width;
                this.y = this.frame.y + this.frame.height * 0.5;
            } else if (this.id === 6) {
                this.x = this.frame.x + this.frame.width * 0.5;
                this.y = this.frame.y + this.frame.height;
            } else if (this.id === 7) {
                this.x = this.frame.x;
                this.y = this.frame.y + this.frame.height * 0.5;
            }
        }

    }
    public render(ctx: CanvasRenderingContext2D) {
        // debuger
        // ctx.beginPath();
        // ctx.arc(this.x ,this.y, 10, 0, Math.PI * 2.0);
        // ctx.closePath();
        // ctx.fillStyle = '#ff0000';
        // ctx.fill();

        if (this.type === CP_TYPE.Endpoint) {
            if (this.id === 0) {
                renderVertex(ctx, this.frame.x, this.frame.y, this.frame.renderer.sizePoint, this.frame.renderer.sizePoint);
            } else if (this.id === 1) {
                renderVertex(ctx, this.frame.x + this.frame.width, this.frame.y, this.frame.renderer.sizePoint, this.frame.renderer.sizePoint);
            } else if (this.id === 2) {
                renderVertex(ctx, this.frame.x + this.frame.width, this.frame.y + this.frame.height, this.frame.renderer.sizePoint, this.frame.renderer.sizePoint);
            } else if (this.id === 3) {
                renderVertex(ctx, this.frame.x, this.frame.y + this.frame.height, this.frame.renderer.sizePoint, this.frame.renderer.sizePoint);
            }
        } else if (this.type === CP_TYPE.Midpoint) {
            if (this.id === 4) {
                renderVertex(ctx, this.frame.x + this.frame.width * 0.5, this.frame.y, this.frame.renderer.sizeMidPoint * 4, this.frame.renderer.sizeMidPoint);
            } else if (this.id === 5) {
                renderVertex(ctx, this.frame.x + this.frame.width, this.frame.y + this.frame.height * 0.5, this.frame.renderer.sizeMidPoint, this.frame.renderer.sizeMidPoint * 4);
            } else if (this.id === 6) {
                renderVertex(ctx, this.frame.x + this.frame.width * 0.5, this.frame.y + this.frame.height, this.frame.renderer.sizeMidPoint * 4, this.frame.renderer.sizeMidPoint);
            } else if (this.id === 7) {
                renderVertex(ctx, this.frame.x , this.frame.y + this.frame.height * 0.5, this.frame.renderer.sizeMidPoint, this.frame.renderer.sizeMidPoint * 4);
            }
        }
    }
}