import { EventController } from '..';
export class MouseInteraction {
    public controller: EventController;
    constructor(core: EventController) {
        this.controller = core;
    }
    public mouseDown(e: MouseEvent) {
        this.controller.pointDown(e.offsetX, e.offsetY);
    }
    public mouseUp(e: MouseEvent) {
        this.controller.isDrag = false;
        this.controller.pointUp(e.offsetX, e.offsetY);
    }
    public mouseWheel(e: WheelEvent, v: number) {
        this.controller.zoom(e.deltaY);
    }
    public mouseMove(e: MouseEvent) {
        if (this.controller.isDown) {
            this.controller.pointDrag(e.offsetX, e.offsetY);
        } else {
            this.controller.pointMove(e.offsetX, e.offsetY);
        }
    }
    public mouseOut(e: MouseEvent) {
        this.controller.pointOut(e.offsetX, e.offsetY);
    }
}