import { EventController } from '..';
export class TouchInteraction {
    public controller: EventController;
    public down = false;
    public touchPre: number[] = [0, 0];
    public zoom: number = 1;
    constructor(controller: EventController) {
        this.controller = controller;
    }
    public touchstart(e: TouchEvent) {
        this.controller.core.renderer.t = 0.0;
        // console.log('touchstart', e);

        var rect = this.controller.core.canvas.canvas.getBoundingClientRect();
        const evX = e.targetTouches[0].pageX - rect.left;
        const evY = e.targetTouches[0].pageY - rect.top;
        this.touchPre[0] = evX;
        this.touchPre[1] = evY;
        this.controller.touchStartRotation = true;
        this.controller.touchStartScale = true;

        this.controller.pointDown(evX, evY);

        e.preventDefault();
    }
    public touchmove(e: TouchEvent) {
        // console.log('touchmove', e);

        if(e.targetTouches.length > 1) {
            this.controller.rotationByTouch(e);
            this.controller.scaleByTouch(e);
            return 
        }

        var rect = this.controller.core.canvas.canvas.getBoundingClientRect();
        const evX = e.targetTouches[0].pageX - rect.left;
        const evY = e.targetTouches[0].pageY - rect.top;
        this.touchPre[0] = evX;
        this.touchPre[1] = evY;

        this.controller.pointDrag(evX, evY);

        e.preventDefault();
    }
    public touchcancel(e: TouchEvent) {
        // console.debug('touchcancel', e);
        this.controller.touchStartRotation = false;
        this.controller.touchStartScale = false;
        e.preventDefault();
    }
    public touchend(e: TouchEvent) {
        this.controller.pointUp(-1, -1);

        e.preventDefault();
    }
}