import { Core } from '../';
import { Controller } from './Controller';
import { Projection, distance } from './Projection';
import { ProjectionSubdiv } from './ProjectionSubdiv';
export enum DISTORTION_TYPE {
    Projection = 'projection',
    Subdivision = 'subdivision'
}
export class Distort {
    public core: Core;
    public projection: Projection;
    public Controllers: Controller[] = [];
    public hostDiv: HTMLElement;
    public hosCanvas: HTMLCanvasElement;
    public hostImg: HTMLImageElement;
    public domRect: DOMRect;
    public id = -1;
    public DISTORTION_TYPE: DISTORTION_TYPE;
    private mPre = [0, 0];
    constructor(core: Core, type = DISTORTION_TYPE.Subdivision) {
        this.core = core;
        this.DISTORTION_TYPE = type;

        this.hostDiv = this.core.app.div;
        this.hosCanvas = this.core.canvas.canvas;
        this.hostImg = this.core.img;
        
        if (this.DISTORTION_TYPE === DISTORTION_TYPE.Projection) {
            this.projection = new ProjectionSubdiv(this);
        } else {
            this.projection = new Projection(this);
        }

        this.init();
    }
    public reset() {
        this.destroyCP();
        this.init();
    }
    public init() {
        const domRect = this.core.canvas.canvas.getBoundingClientRect();
        this.domRect = domRect;
        // console.log(this.core.canvas.canvas.style.left);
        // console.log(domRect)

        this.Controllers = [
            new Controller(this, 0, domRect.x, domRect.y),
            new Controller(this, 1, domRect.x + domRect.width, domRect.y),
            new Controller(this, 2, domRect.x + domRect.width, domRect.y + domRect.height),
            new Controller(this, 3, domRect.x, domRect.y + domRect.height),
        ]
    }
    public distortPerspective(v: number) {
        this.projection.distortPerspective(v);
        this.renderForce();
    }
    public updateCaptured(x: number, y: number) {
        this.id = -1;
        for (let i = 0 ; i < this.Controllers.length; ++i) {
            if (distance(x + this.domRect.x , y + this.domRect.y, this.Controllers[i].x, this.Controllers[i].y) < 50) {
                this.id = this.Controllers[i].id;
            }
        }
    }
    public mouseDown(x: number, y: number) {
        this.updateCaptured(x,y)
        this.mPre = [x, y] 
    }
    public mouseUp(x: number, y: number) {
        this.id = -1;
    }
    public mouseMove(x: number, y: number) {
        if (this.id > -1) {
            const xOff = x - this.mPre[0];
            const yOff = y - this.mPre[1];
            this.Controllers[this.id].translation(xOff, yOff);
            this.mPre = [x, y] 
        }
    }
    public render(ctx: CanvasRenderingContext2D) {
        this.projection.renderDistortion(ctx, this.hostImg);
    }
    public renderForce() {
        this.core.canvas.renderForce();
    }
    public destroyCP() {
        try {
            this.Controllers.forEach((e: Controller) => {
                e.destroy();
            });
        } catch (error) {
            
        }

    }
}



