import { Core } from '..';
export class Canvas {

    public core: Core;
    public div: HTMLElement;
    public canvas: HTMLCanvasElement;
    public ctx: CanvasRenderingContext2D;
    public isActive: boolean = false;

    public log: string;
    public isViewChanging: boolean = false;
    public loopNum: number = 0;
    public needRenderloop: boolean = true;
    public ablePan: boolean = true;

    public mousePre: number [] = [0, 0];
    public canvasOut: HTMLCanvasElement;
    public ctxOut: CanvasRenderingContext2D;
    public loopNeeded = false;

    constructor(core: Core, div: HTMLElement) {
        this.core = core;
        this.div = div;
        this.loopNeeded = false;
        this.init();
        this.loopInit();
    }
    private init() {
        const canvas = document.createElement('canvas');
        const canvasOut = document.createElement('canvas');
        if (canvas && canvasOut) {
            this.canvas = canvas;
            this.canvas.width = this.div.clientWidth;
            this.canvas.height = this.div.clientHeight;
            this.div.append(this.canvas);

            this.canvasOut = canvasOut;

            this.canvasOut.style.border = '5px';
            this.ctxOut = this.canvasOut.getContext('2d') as CanvasRenderingContext2D;
            this.core.app.divHost.append(this.canvasOut);

            const ctx = this.canvas.getContext('2d');
            if (ctx) {
                this.ctx = ctx;
                this.isActive = true;
                this.initEvent();
            }
        }
    }
    public initEvent() {
        this.canvas.addEventListener('keydown', (k: KeyboardEvent) => {});
        this.canvas.addEventListener('keyup', (k: KeyboardEvent) => {});

        // eslint-disable-next-line @typescript-eslint/no-unused-expressions
        this.canvas.addEventListener('mousedown', (e: MouseEvent) => { this.core.controller.mouseInteraction.mouseDown(e); }, false);
        this.canvas.addEventListener('mouseup', (e: MouseEvent) => { this.core.controller.mouseInteraction.mouseUp(e); }, false);
        this.canvas.addEventListener('mousemove', (e: MouseEvent) => { this.core.controller.mouseInteraction.mouseMove(e); }, false);
        this.canvas.addEventListener('wheel', (e: WheelEvent) => this.core.controller.mouseInteraction.mouseWheel(e, e.deltaY), false);
        this.canvas.addEventListener('mouseout', (e: MouseEvent) => { this.core.controller.mouseInteraction.mouseOut(e); }, false);

        this.canvas.addEventListener('touchstart', (t: TouchEvent) => this.core.controller.touchInteraction.touchstart(t), false);
        this.canvas.addEventListener('touchmove', (t: any) => this.core.controller.touchInteraction.touchmove(t), false);
        this.canvas.addEventListener('touchcancel', (t: TouchEvent) => this.core.controller.touchInteraction.touchcancel(t), false);
        this.canvas.addEventListener('touchend', (t: TouchEvent) => this.core.controller.touchInteraction.touchend(t), false);
    }
    public capture() {
        const img = new Image();
        img.crossOrigin = 'anonymous';
        img.src = this.canvasOut.toDataURL();
        return img;
    }
    private loopInit() {
        this.loop();
    }
    public resize() {
        this.canvas.width = this.div.clientWidth;
        this.canvas.height = this.div.clientHeight;
    }
    public loop() {
        requestAnimationFrame(() => { this.loop(); });

        if (this.loopNum++ < 130) {

            let before = performance.now();
            this.renderPre(this.ctx);

            let after = performance.now();
            this.log = `level: , total frames: ${this.loopNum++} (FPS: ), a loop took: ${(after - before).toFixed(2)} ms to execute.`;
            // console.debug(this.log);
        }
    }
    public clear(ctx: CanvasRenderingContext2D) {
        this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
    }
    public renderForce() {
        // if (!this.loopNeeded) {
            // this.loop();
        this.loopNum = 0;
        // }
    }
    public renderPre(ctx: CanvasRenderingContext2D) {
        if (!this.isActive) { return; }
        this.clear(this.ctx);
        this.render(ctx);
    }
    public render(ctx: CanvasRenderingContext2D) {
        // ctx.beginPath();
        // ctx.arc(0, 0, 15, 0, 3.14 * 2);
        // ctx.closePath()
        // ctx.fill();
    }
}