import { Core } from '..';
import { TouchInteraction } from './Event/TouchInteraction';
import { MouseInteraction } from './Event/MouseInteraction';
import { ActiveCursor } from './ActiveCursor';
export class EventController {

    public core: Core;
    public activeCursor: ActiveCursor;
    public mouseInteraction: MouseInteraction;
    public touchInteraction: TouchInteraction;

    public isDown = false;
    public isDrag = false;

    public mousePre = [0, 0];
    public zoomLevel: number = 1;
    public rotationRadian: number = 0;
    // public translation: number[] = [0, 0];

    // touch for rotation
    private scalePre = 1;
    private anglePre = 0;
    public touchStartRotation = false;
    public touchStartScale = false;

    constructor(core: Core) {
        this.core = core;
        this.activeCursor = new ActiveCursor();
        this.touchInteraction = new TouchInteraction(this);
        this.mouseInteraction = new MouseInteraction(this);

    }
    public import(url: string) {
        this.reset();
    }
    public reset() {
        this.core.distort.destroyCP();
    }
    public updateWidthByInput(v: number) {
        if (v < this.core.frame.widthMin) {
            this.core.frame.width = this.core.frame.widthMin;
        } else if (v + this.core.frame.x > this.core.imageWrapper.img.width) {
            this.core.frame.width = this.core.imageWrapper.img.width - this.core.frame.x;
        } else {
            this.core.frame.width = v;
        }
        this.updateSizeByInputPost();
    }
    public updateHeightByInput(v: number) {
        if (v < this.core.frame.heightMin) {
            this.core.frame.height = this.core.frame.heightMin;
        } else if (v + this.core.frame.y > this.core.imageWrapper.img.height) {
            this.core.frame.height = this.core.imageWrapper.img.height - this.core.frame.y;
        } else {
            this.core.frame.height = v;
        }
        this.updateSizeByInputPost();
    }
    private updateSizeByInputPost() {
        this.core.frame.update();
        this.core.canvas.renderForce();
        this.core.canvas.canvasOut.width = this.core.frame.width;
        this.core.canvas.canvasOut.height = this.core.frame.height;
    }
    public offsetByInput(v: number) {
        // console.log(v, this.core.frame.idSelected);

        if (this.core.frame.idSelected === 0 || this.core.frame.idSelected === 4 || this.core.frame.idSelected === 7 ) {
            v *= -1;
        }

        this.core.frame.offsetByID(this.core.frame.idSelected, v, v);
        this.core.renderer.updateCanvasCapture();

    }
    public updateCursor() {
        if (this.core.frame.isHover) {
            // console.log(this.core.frame.idHover)
            if (this.core.frame.idHover === -1) {
                this.activeCursor.dragging();
            } else if (this.core.frame.idHover === 0 || this.core.frame.idHover === 2) {
                this.activeCursor.set_nw_resize();
            } else if (this.core.frame.idHover === 1 || this.core.frame.idHover === 3) {
                this.activeCursor.set_ne_resize();
            } else if (this.core.frame.idHover === 5 || this.core.frame.idHover === 7) {
                this.activeCursor.set_e_resize();
            } else if (this.core.frame.idHover === 4 || this.core.frame.idHover === 6) {
                this.activeCursor.set_ns_resize();
            }
        } else {
            this.activeCursor.default();
        }
    }
    public rotate(v: number) {
        this.rotationRadian = v;
        this.postUpdate();
    }
    public pointDown(x: number, y: number) {
        this.isDown = true;
        this.core.frame.isHoverCheck(x, y);
        this.core.distort?.mouseDown(x, y);
        this.mousePre[0] = x;
        this.mousePre[1] = y;

        this.postUpdate();
    }
    public pointUp(x: number, y: number) {
        this.isDown = false;
        this.core.frame.mouseUp();
        this.core.distort?.mouseUp(x, y);
        this.postUpdate();
    }
    public pointOut(x: number, y: number) {
        this.isDown = false;
        this.core.frame.isHover = false;
        this.isDrag = false;
        // this.anglePre = 0.0;
        this.postUpdate();
    }
    public pointMove(x: number, y: number) {
        this.core.frame.isHoverCheck(x, y);

        this.mousePre[0] = x;
        this.mousePre[1] = y;

        this.updateCursor();

        this.core.distort?.mouseMove(x, y);
        
        this.postUpdate();
    }
    public pointDrag(x: number, y: number) {
        this.isDrag = true;
        // this.core.frame.isHover(x, y);
        // console.log(x, y);

        if (this.core.frame.isHover) {
            this.mouseDragRect(x, y);
        } else {
            this.mouseDragPan(x, y);
        }
        this.core.distort?.mouseMove(x, y);
        // return;

        this.postUpdate();
    }
    public mouseDragRect = (x: number, y: number) => {
        const xOff = x - this.mousePre[0];
        const yOff = y - this.mousePre[1];
        this.core.frame.translate(xOff, yOff);


        // let cos = Math.cos(angle * (3.14159265358979 / 180.0));
        // let sin = Math.sin(angle * (3.14159265358979 / 180.0));
        // let x = v.x * cos - v.y * sin;
        // let y = v.x * sin + v.y * cos;


        // this.translation[0] += xOff;
        // this.translation[1] += yOff;


        this.mousePre[0] = x;
        this.mousePre[1] = y;
        // this.pointDrag(x, y);
    }
    public mouseDragPan = (x: number, y: number) => {
        this.pan(x, y);
    }
    public zoom(v: number) {
        // console.log(e.deltaY);
        if (v < 0) {
            this.zoomLevel += this.zoomLevel * 0.19;
        } else {
            this.zoomLevel -= this.zoomLevel * 0.19;
        }

        this.postUpdate();
    }
    // public RemapByZoom(e: MouseEventData) {
    //     e.x = (e.x - this.translation[0]) / this.zoom;
    //     e.y = (e.y - this.translation[1]) / this.zoom;
    //     return e;
    // }
    public pan(x: number, y: number) {
        // if (!this.ablePan) { return;}
        // const xOff = e.x - this.mousePre[0];
        // const yOff = e.y - this.mousePre[1];
        // this.translation[0] += xOff;
        // this.translation[1] += yOff;
        // console.log(this.translation);
    }
    public postUpdate() {
        this.core.canvas.renderForce();
    }
    public downloadImage() {
        // this.core.canvas.canvasOut;

        const a = document.createElement('a');
        a.href = this.core.canvas.canvasOut.toDataURL();
        a.download = 'image.png';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
    }
    public rotationByTouch(e: TouchEvent) {
        this.core.canvas.renderForce();

        let t0;
        let t1;
        if (e.targetTouches[0].clientY < e.targetTouches[1].clientY) {
            t0 = e.targetTouches[0];
            t1 = e.targetTouches[1];
        } else {
            t0 = e.targetTouches[1];
            t1 = e.targetTouches[0];
        }

        const angle = Math.atan2(t1.clientX - t0.clientX, t1.clientY - t0.clientY );
        const theAngle =  this.anglePre - angle; 

        // it happens one time when touch starts
        if (this.touchStartRotation) {
            this.anglePre = this.rotationRadian;
            this.touchStartRotation = false;
            this.anglePre = angle;
            return;
        }

        this.rotationRadian += theAngle;
        this.anglePre = angle;
    }
    public scaleByTouch(e: TouchEvent) {
        this.core.canvas.renderForce();

        let t0;
        let t1;
        if (e.targetTouches[0].clientY < e.targetTouches[1].clientY) {
            t0 = e.targetTouches[0];
            t1 = e.targetTouches[1];
        } else {
            t0 = e.targetTouches[1];
            t1 = e.targetTouches[0];
        }

        const distance = Math.sqrt( 
            (t1.clientX - t0.clientX) * (t1.clientX - t0.clientX) +  
            (t1.clientY - t0.clientY) * (t1.clientY - t0.clientY)
        );



        if (this.scalePre > distance) {
            if (this.zoomLevel > 0.4) {
                this.zoomLevel -= 0.015;
            }
        } else {
            if (this.zoomLevel < 2.0) {
                this.zoomLevel += 0.015;
            }
        }

        // const theZoom = this.scalePre - distance * 0.001; 

        // it happens one time when touch starts
        if (this.touchStartScale) {
            this.scalePre = this.zoomLevel;
            this.touchStartScale = false;
            this.scalePre = distance;
            return;
        }

        

        // console.log(theZoom);
        // this.zoomLevel += theZoom;
        this.scalePre = distance;
    }
}