import { Distort } from '..';
export class Controller {
    public distort: Distort;
    public width = 20;
    public height = 20;
    public id: number;
    public x: number;
    public y: number;
    public dom: SVGSVGElement;
    constructor(distort: Distort, id: number, x: number, y: number) {
        this.distort = distort;
        this.id = id;
        this.x = x;
        this.y = y;
        this.dom = getRectSVG(this.width, this.height, 5);
        this.dom.style.pointerEvents = 'none';
        this.distort.hostDiv.appendChild(this.dom);
        this.move(x, y);
    }
    public translation(x: number, y: number) {
        this.x = parseInt(this.dom.style.left) + x;
        this.y = parseInt(this.dom.style.top) + y;
        this.dom.style.left = this.x + 'px';
        this.dom.style.top = this.y + 'px';
        // console.log(this.dom.style.left);
    }
    public move(x: number, y: number) {
        this.x = x - this.width * 0.5;
        this.y = y - this.height * 0.5;
        this.dom.style.left = this.x + 'px';
        this.dom.style.top = this.y + 'px';
    }
    public destroy() {
        this.dom.parentElement.removeChild(this.dom);
    }   
}
export const getRectSVG = (width: number = 8, height: number = 8, fillet: number = 2, fillStyle: string = '#ffffff', strokeWidth: number = 1, strokeStyle: string = '#CBDAE2' ) => {
    let rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');
    rect.setAttribute('x','1');
    rect.setAttribute('y','1');
    rect.setAttribute('width', `${width}`);
    rect.setAttribute('height',`${height}`);
    rect.setAttribute('stroke-width', `${strokeWidth}`);
    rect.setAttribute('rx', `${fillet}`);
    rect.setAttribute('fill', `${fillStyle}`);
    rect.setAttribute('stroke', `${strokeStyle}`);

    let rectSVG = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    rectSVG.appendChild(rect);
    rectSVG.setAttribute('width', `${width + strokeWidth * 2}`);
    rectSVG.setAttribute('height', `${height + strokeWidth * 2}`);
    rectSVG.style.position = 'absolute';
    return rectSVG;
};