// import { distance } from '../../lib';
import { Renderer } from './Core/Renderer';
import { ControlPoint, CP_TYPE } from './Core/ControlPoint';
import { Transform } from './Core/Transform';
import { Interaction } from './Core/Interaction';
import { Core } from '..';


export enum FramePreset {
    Freeform = 'freeform',
    Square = 'square',
    Portrait = 'portrait',
    Landscape = 'landscape',

    Fixed_1_1 = 'fixed_1_1',
    Fixed_16_9 = 'fixed_16_9'
}


export class Frame {
    public renderer: Renderer;
    public transform: Transform;
    public interaction: Interaction;
    public idHover = -1;
    public idSelected = -1;
    public isHover = false;

    public framePreset: FramePreset;

    public x = 100;
    public y = 100;
    public widthMin = 200;
    public heightMin = 200;
    public width = 300;
    public height = 300;

    public xOffGuid = 0;
    public yOffGuid = 0;

    public r = 19;

    public cv: number[] = [0, 0];
    public core: Core;

    public cpList: ControlPoint[] = [];

    constructor(core: Core) {
        this.core = core;
        this.init();
    }
    public init() {
        this.xOffGuid = this.width / 3;
        this.yOffGuid = this.height / 3;

        this.cpList = [
            new ControlPoint(this, 0, CP_TYPE.Endpoint),
            new ControlPoint(this, 1, CP_TYPE.Endpoint),
            new ControlPoint(this, 2, CP_TYPE.Endpoint),
            new ControlPoint(this, 3, CP_TYPE.Endpoint),

            new ControlPoint(this, 4, CP_TYPE.Midpoint),
            new ControlPoint(this, 5, CP_TYPE.Midpoint),
            new ControlPoint(this, 6, CP_TYPE.Midpoint),
            new ControlPoint(this, 7, CP_TYPE.Midpoint),
        ];

        if (this.core.app.param.colorCover) {
            // this.fillStyle = this.core.app.param.colorCover;
            // this.fillStyleHover = hexToCanvasColor(this.core.app.param.colorCover, 0.5);
        }

        this.interaction = new Interaction(this);
        this.transform = new Transform(this);
        this.renderer = new Renderer(this);
        // this.update();

    }
    public setRatio(v: FramePreset.Freeform) {
        this.framePreset = v;
    }
    public InitFrame() {
        this.width = 200;
        this.height = 200;

        if (this.core.img) {
            this.width = this.core.img.width;
            this.height = this.core.img.height;
        }

        this.xOffGuid = this.width / 5;
        this.yOffGuid = this.height / 5;

        this.x = this.xOffGuid;
        this.y = this.yOffGuid;

        this.width = Math.floor(this.width - this.xOffGuid * 2);
        this.height = Math.floor(this.height - this.yOffGuid * 2);

        this.cv[0] = this.x + this.width * 0.5;
        this.cv[1] = this.y + this.height * 0.5;

        this.core.canvas.canvasOut.width = this.core.frame.width;
        this.core.canvas.canvasOut.height = this.core.frame.height;

        this.update();

        this.updateUIWidthHeight();
    }
    public updateColorBG(r: number, g: number, b: number) {
        this.renderer.updateColorBG(r, g, b);
    }
    public update() {
        for (let i = 0 ; i < this.cpList.length; ++i) {
            this.cpList[i].update();
        }
        this.transform.updateGuides();
    }
    public isHoverCheck(x: number, y: number) {
        return this.interaction.isHoverCheck(x, y);
    }
    public mouseUp() {
        return this.interaction.mouseUp();
    }
    public offsetByID(id: number, xOff: number, yOff: number) {
        return this.transform.offsetByID(id, xOff, yOff);
    }
    public render(ctx: CanvasRenderingContext2D) {
        this.renderer.render(ctx);
    }
    public translate(xOff: number, yOff: number) {

        // do not allow to move the frame out of the extent
        if (this.x + xOff < 0 ||
            this.x + this.width + xOff > this.core.canvas.canvas.width ||
            this.y + yOff < 0 ||
            this.y + this.height + yOff > this.core.canvas.canvas.height ) {
            return;
        }

        this.offsetByID(this.idHover, xOff, yOff);
        this.updateUIWidthHeight();

    }
    public updateUIWidthHeight() {
        // console.log('updateUIWidthHeight', this.width, this.height);

        this.core.app.ui.divWidth.value = `${this.width}`;
        this.core.app.ui.divHeight.value = `${this.height}`;
        this.update();
        this.core.canvas.renderForce();
    }
}