import { Frame } from '..';
export class Transform {
    frame: Frame;
    constructor(frame: Frame) {
        this.frame = frame;
    }
    public offsetByID(id: number, xOff: number, yOff: number) {

        switch (id) {
            case 0:
                this.frame.x += xOff;
                this.frame.y += yOff;
                this.frame.width -= xOff;
                this.frame.height -= yOff;
                break;
            case 1:
                this.frame.width += xOff;
                this.frame.y += yOff;
                this.frame.height -= yOff;
                break;
            case 2:
                this.frame.width += xOff;
                this.frame.height += yOff;
                break;
            case 3:
                this.frame.x += xOff;
                this.frame.width -= xOff;
                this.frame.height += yOff;
                break;
            case 4:
                this.frame.y += yOff;
                this.frame.height -= yOff;
                break;
            case 5:
                this.frame.width += xOff;
                break;
            case 6:
                this.frame.height += yOff;
                break;
            case 7:
                this.frame.x += xOff;
                this.frame.width -= xOff;
                break;
            default:
                this.frame.x += xOff;
                this.frame.y += yOff;
                break;
        }

        this.updateGuides();
        this.frame.update();
        this.frame.core.canvas.renderForce();
    }
    public updateGuides() {
        this.frame.xOffGuid = this.frame.width / 3;
        this.frame.yOffGuid = this.frame.height / 3;
        this.frame.cv[0] = this.frame.x + this.frame.width * 0.5;
        this.frame.cv[1] = this.frame.y + this.frame.height * 0.5;
    }
}