import { Frame } from '..';
export class Interaction {
    frame: Frame;
    constructor(frame: Frame) {
        this.frame = frame;
    }
    public mouseUp() {
        this.frame.isHover = false;
        if (this.frame.idHover !== -1) {
            this.frame.idSelected = this.frame.idHover;
        }
    }
    public isHoverCheck(x: number, y: number) {

        for (let i = 0; i < this.frame.cpList.length; ++i) {
            const idCP = this.frame.cpList[i].isHoverCheck(x, y);
            if (idCP !== -1) {
                this.frame.isHover = true;
                this.frame.idHover = idCP;
                return true;
            }
        }

        if (this.frame.x < x  && this.frame.x + this.frame.width > x &&
            this.frame.y < y  && this.frame.y + this.frame.height > y) {
            this.frame.idHover = -1;
            this.frame.isHover = true;
            return true;
        }

        this.frame.idHover = -1;
        this.frame.isHover = false;
        return false;
    }
}