import { ExpreeImageTool } from '..';
export class UI {
    public expreeImageTool: ExpreeImageTool;
    public divUI: HTMLElement;

    public divSizeFrameBG: HTMLElement;
    public divWidth: HTMLInputElement;
    public divHeight: HTMLInputElement;

    constructor(expreeImageTool: ExpreeImageTool) {
        this.expreeImageTool = expreeImageTool;

        const txt = document.createElement('p');
        txt.textContent = 'Image Distortion';
        txt.style.fontSize = '12px;';
        txt.style.color = '#afafaf';
        txt.style.position = 'absolute';
        txt.style.top = '0px';
        txt.style.left = '20px';
        this.expreeImageTool.divHost.append(txt);

        const divUI = document.createElement('div') as HTMLElement;
        this.divUI = divUI;
        this.divUI.style.width = '100%';
        this.divUI.style.height = '100px';
        this.divUI.style.backgroundColor = 'rgba(0,0,0,0)';
        this.expreeImageTool.divHost.append(this.divUI);
    }
    public updateStyle(r: number, g: number, b: number) {
        this.divSizeFrameBG.style.backgroundColor = `rgb(${r},${b},${g})`;
    }
    public initPost() {

        this.divSizeFrameBG = document.createElement('div');
        this.divSizeFrameBG.style.textAlign = 'center';
        this.divSizeFrameBG.style.backgroundColor = '#afafaf';
        this.divSizeFrameBG.style.padding = '10px';
        this.divSizeFrameBG.textContent = 'Size ';


        this.divWidth = document.createElement('input');
        this.divWidth.style.width = '50px';
        this.divWidth.setAttribute('type', 'text');
        this.divWidth.setAttribute('min', '100');
        // this.divWidth.setAttribute('max', '500');
        // this.divWidth.setAttribute('value', '300');
        this.divSizeFrameBG.append(this.divWidth);
        this.divWidth.oninput = () => {
            const value = Number.parseFloat(this.divWidth.value);
            if (value) {
                this.expreeImageTool.core.controller.updateWidthByInput(value);
            }
        };

        this.divHeight = document.createElement('input');
        this.divHeight.style.width = '50px';
        this.divHeight.setAttribute('type', 'text');
        this.divHeight.setAttribute('min', '100');
        // this.divHeight.setAttribute('max', '50');
        // this.divHeight.setAttribute('value', '10');
        this.divSizeFrameBG.append(this.divHeight);
        this.divHeight.oninput = () => {
            const value = Number.parseFloat(this.divHeight.value);
            if (value) {
                this.expreeImageTool.core.controller.updateHeightByInput(value);
            }
        };

        this.divUI.append(this.divSizeFrameBG);


        let btn = document.createElement('button');
        btn.textContent = 'Reset';
        this.divUI.append(btn);
        btn.onclick = () => {
            // console.log('play.....');
            this.expreeImageTool.core.controller.reset();
        };


        btn = document.createElement('button');
        btn.textContent = 'Download';
        this.divUI.append(btn);
        btn.onclick = () => {
            // console.log('play.....');
            this.expreeImageTool.core.controller.downloadImage();
        };


        btn = document.createElement('button');
        btn.textContent = 'Capture';
        this.divUI.append(btn);
        btn.onclick = () => {
            const img = this.expreeImageTool.core.canvas.capture();
            // console.log(img);
            this.divUI.append(img);
        };


        let slider = document.createElement('input');
        slider.type = 'range';
        slider.min = '-3.13';
        slider.max = '3.13';
        slider.step = '0.01';
        slider.value = '0';
        slider.oninput = () => {
            // console.log(slider.value);
            // this.expreeImageTool.core.distort.distortPerspective(+slider.value);
        };
        this.divUI.append(slider);


        const divURL = document.createElement('input');
        // divURL.textContent = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.png';
        divURL.setAttribute('type', 'text');
        divURL.setAttribute('value', 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.png');
        this.divUI.append(divURL);
        divURL.oninput = (input) => {
            this.expreeImageTool.core.controller.reset();
            this.expreeImageTool.core.importImage(divURL.textContent as string);
        };
        divURL.addEventListener('keyup', (event: KeyboardEvent) => {
            if (event.keyCode === 13) { // Enter
                event.preventDefault();
                this.expreeImageTool.core.controller.reset();
                this.expreeImageTool.core.importImage(divURL.value as string);
            }
        });
        btn = document.createElement('button');
        btn.textContent = '<';
        this.divUI.append(btn);
        btn.onclick = () => {
            this.expreeImageTool.core.distort.distortPerspective(-5);
        };

        btn = document.createElement('button');
        btn.textContent = '>';
        this.divUI.append(btn);
        btn.onclick = () => {
            this.expreeImageTool.core.distort.distortPerspective(5);
        };

        btn = document.createElement('button');
        btn.textContent = 'Reset';
        this.divUI.append(btn);
        btn.onclick = () => {
            this.expreeImageTool.core.distort.reset();
        };

        // var x = document.createElement('INPUT');
        // x.setAttribute('type', 'text');
        // x.setAttribute('value', 'Hello World!');

        let divTitle = document.createElement('div');
        divTitle.textContent = 'Offset: ';
        divTitle.style.padding = '10px';

        let divInput = document.createElement('input');
        divInput.style.width = '30px';
        divInput.setAttribute('type', 'text');
        divInput.setAttribute('min', '-50');
        divInput.setAttribute('max', '50');
        divInput.setAttribute('value', '10');

        // divInput.
        // divInput.setAttribute('min', '10');
        divTitle.append(divInput);
        this.divUI.append(divTitle);
        divInput.addEventListener('keyup', (event: KeyboardEvent) => {
            if (event.keyCode === 13) { // Enter
                event.preventDefault();
                console.log('divInput.value', divInput.value);

                let value = Number.parseFloat(divInput.value);

                this.expreeImageTool.core.controller.offsetByInput(value);
                // console.log(value);
                // if(divInput.value.includes('-')) {
                //     console.log('-', divInput.value.includes('-'));

                //     this.expreeImageTool.core.controller.offsetByInput(Number.parseFloat(divInput.value) * -1);
                // } else {
                //     this.expreeImageTool.core.controller.offsetByInput(Number.parseFloat(divInput.value));
                //     console.log('+', divInput.value.includes('-'));
                // }
            }
        });


        // let divRatio = document.createElement('div');
        // divRatio.textContent = 'ddddd: ';
        // divRatio.style.padding = '10px';
        // this.divUI.append(divRatio)

        btn = document.createElement('button');
        btn.textContent = 'dd';
        // btn.style.float = 'left';
        this.divUI.append(btn);
        btn.onclick = () => {
            const img = this.expreeImageTool.core.canvas.capture();
            // console.log(img);
            this.divUI.append(img);
        };
        // // .............................................
        // divTitle = document.createElement('div');
        // // divTitle.style.float =
        // // divTitle.textContent = 'Offset: '

        // let divText= document.createElement('div');
        // divText.textContent = 'x: ';
        // divText.style.padding = '10px';
        // divTitle.append(divText);

        // divInput = document.createElement('input');
        // // divInput.style.borderLeft = '10px';
        // divInput.setAttribute('type', 'text');
        // divInput.setAttribute('value', '5');
        // divTitle.append(divInput);
        // this.divUI.append(divTitle);
        // divInput.addEventListener('keyup', (event: KeyboardEvent) => {
        //     if (event.keyCode === 13) { // Enter
        //         event.preventDefault();
        //         this.expreeImageTool.core.controller.offsetByInput(+divInput.value);
        //     }
        // });

        // divInput = document.createElement('input');
        // divInput.style.borderLeft = '10px';
        // divInput.setAttribute('type', 'text');
        // divInput.setAttribute('value', '5');
        // this.divUI.append(divInput);
        // divInput.addEventListener('keyup', (event: KeyboardEvent) => {
        //     if (event.keyCode === 13) { // Enter
        //         event.preventDefault();
        //         this.expreeImageTool.core.controller.offsetByInput(+divInput.value);
        //     }
        // });

    }
}
