class KNNColor {

  public ncols: NColor[];
  public k: number;
  public minDiff: number;
  public clusters: Cluster[];

  constructor( _ncols: NColor[], _k = 3, _minDiff = 1) {
    this.ncols = _ncols;
    if (_minDiff < 1) { _minDiff = 1; }
    this.k = _k;
    this.minDiff = _minDiff;
    this.init();
  }
  private init() {
    console.log('a');

    let rnd = getRandomBySeed(10);

    let clusters: Cluster[] = [];
    let seen: number[] = [];

    console.log('b');

    while (clusters.length < this.k) {
      let idx = Math.floor(rnd() * this.ncols.length);
      // let idx = Math.floor(Math.random() * this.ncols.length);
      let found = false;
      // console.log(idx);

      for (let i = 0; i < seen.length; i++ ) {
        if (idx === seen[i]) {
          found = true;
          break;
        }
      }
      if (!found) {
        seen.push(idx);
        let cluster = new Cluster(this.ncols[idx]);
        cluster.push(this.ncols[idx]);
        clusters.push(cluster);
      }
    }

    console.log('c');
    let safeguard = 0;

    while (true) {

      if (safeguard++ > 10000  ) { console.log('braek'); break; }
      let plists: NColor[][] = [];
      for (let i = 0; i < this.k; i++) {
        plists.push([]);
      }

      for (let j = 0; j < this.ncols.length; j++) {
        let p: NColor = this.ncols[j];
        let smallest_distance = Number.MAX_VALUE;
        let idx = 0;
        for (let i = 0; i < this.k; i++) {
          let distance = this.euclidean(p, clusters[i].key);
          if (distance < smallest_distance) {
            smallest_distance = distance;
            idx = i;
          }
        }
        plists[idx].push(p);
      }
      // console.log('d');
      // console.log(plists);
      let diff = 0;
      for (let i = 0; i < this.k; i++) {

        let old: NColor = clusters[i].key;

        let center: NColor = this.calculateCenter(plists[i], 3);  // what is 3
        // console.log(center, plists[i])
        let newCluster = new Cluster(center);
        newCluster.values = plists[i];
        let dist = this.euclidean(old, center);
        console.log(dist);
        if (isNaN(dist)) {
          // dist = 0.0;
          continue;
        }
        clusters[i] = newCluster;
        diff = diff > dist ? diff : dist;
      }
      // console.log('e');
      console.log('diff', diff);
      if (diff < this.minDiff) { break; }
      // break;
    }
    console.log('done!');
    this.clusters = clusters;
  }
  private euclidean(p1: NColor, p2: NColor) {
    // console.log(p1, p2);
    let s = Math.pow(p1.r - p2.r, 2) + Math.pow(p1.g - p2.g, 2) + Math.pow(p1.b - p2.b, 2);
    // console.log(s);
    return Math.sqrt(s);
  }
  private calculateCenter( cols: NColor[],  n: number) {
    let cenCol = new NColor(0, 0, 0);
    for (let i = 0; i < cols.length; i++) {
      cenCol.r += cols[i].r;
      cenCol.g += cols[i].g;
      cenCol.b += cols[i].b;
    }
    cenCol.r /= cols.length;
    cenCol.g /= cols.length;
    cenCol.b /= cols.length;
    return cenCol;
  }

}
class Cluster {
  public key: NColor;
  public values: NColor[] = [];

  constructor(_key: NColor) {
    this.key = _key;
  }
  public push(_Value: NColor) {
    this.values.push(_Value);
  }
  public getKey() {
    return [this.key.r, this.key.g, this.key.b];
  }
}
class NColor {
  public r = 0;
  public g = 0;
  public b = 0;
  constructor( _r = 0, _g = 0, _b = 0) {
    this.r = _r;
    this.g = _g;
    this.b = _b;
  }
}
const getRandomBySeed = (seed: number) => {
  if (seed === undefined) {
    seed = performance.now();
  }
  return () => {
    seed = (seed * 9301 + 49297) % 233280;
    return seed / 233280;
  };
};
