export const thumbnail = (img: HTMLImageElement, maxWidth: number, maxHeight: number) => {

  // Create and initialize two canvas
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d') as CanvasRenderingContext2D;
  const canvasCopy = document.createElement('canvas');
  const copyContext = canvasCopy.getContext('2d') as CanvasRenderingContext2D;

  // Determine new ratio based on max size
  let ratio = 1;
  if (img.width > maxWidth) {
    ratio = maxWidth / img.width;
  } else if (img.height > maxHeight) {
    ratio = maxHeight / img.height;
  }
  // Draw original image in second canvas
  canvasCopy.width = img.width;
  canvasCopy.height = img.height;
  copyContext.drawImage(img, 0, 0);

  // Copy and resize second canvas to first canvas
  canvas.width = img.width * ratio;
  canvas.height = img.height * ratio;
  ctx.drawImage(canvasCopy, 0, 0, canvasCopy.width, canvasCopy.height, 0, 0, canvas.width, canvas.height);

  let imgOut = new Image();
  imgOut.src = canvas.toDataURL();
  imgOut.width = canvas.width;
  imgOut.height = canvas.height;
  return imgOut;

};
export const process_image = (img: HTMLImageElement, k: number = 1 ) => {
  const points: NColor[] = [];
  console.log('1');
  const width = 100;
  const height = 100;
  // img = thumbnail(img, width, height)
  // let imgDiv = document.getElementById('theimg');
  // imgDiv.append(img);
  // ctx.drawImage(img, 0, 0, img.width, img.height);
  // console.log(img.width, img.height);
  // let data = ctx.getImageData(0, 0, img.width, img.height).data;
  console.log('2');
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d') as CanvasRenderingContext2D;
  ctx.drawImage(img, 0, 0, width, height);
  console.log('3');
  const data = ctx.getImageData(0, 0, width, height).data;
  for (let i = 0, l = data.length; i < l;  i += 4) {
    let r = data[i];
    let g = data[i + 1];
    let b = data[i + 2];

    // console.log(r,g,b);
    points.push( new NColor(r, g, b));
  }
  console.log('4');
  let results = new KNNColor(points, k, 1);
  console.log('5');
  let hex = [];
  for (let i = 0; i < results.clusters.length; i++) {
    hex.push(results.clusters[i].getKey());
  }
  return hex;
};