export const distance = (x0: number, y0: number, x1: number, y1: number) => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));
};
export const renderVertex = (ctx: CanvasRenderingContext2D, x: number, y: number, width: number, height: number, r: number = 0.5) => {
    const angle = 3.14 * 0.5;
    ctx.beginPath();
    ctx.arc(x + width - r, y - height + r, r, angle * 3 , angle * 4 , false);
    ctx.arc(x + width - r, y + height - r, r, angle * 0 , angle * 1 , false);
    ctx.arc(x - width + r, y + height - r, r, angle * 1 , angle * 2 , false);
    ctx.arc(x - width + r, y - height + r, r, angle * 2 , angle * 3 , false);
    ctx.closePath();
    ctx.strokeStyle  = '#000000';
    ctx.fillStyle   = '#ffffff';
    ctx.fill();
    ctx.stroke();
};
export const linearColorInterpolation = (c0: number[], c1: number[], interpolation = 0.5) => {
    const cOut = [];
    for (let i = 0 ; i < c0.length; ++i) {
        const c = c0[i] + (c1[i] - c0[i]) * interpolation;
        cOut.push(c);
    }
    return cOut;
};
export const hexToCanvasColor = (hex: string, visibility: number | string = 0, isGray: boolean = false) => {
    // if (hex[0] !== '#') {
    //     let col = hex.split('(')[1].split(')')[0].split(',');
    //     hex = this.RGBToHex(parseInt(col[0]), parseInt(col[1]), parseInt(col[2]));
    // }
    // Convert #AA77CC to rbga() format for Firefox
    let thecol;
    visibility = visibility || '1.0';
    hex = hex.replace('#', '');
    let r = parseInt(hex.substring(0, 2), 16);
    let g = parseInt(hex.substring(2, 4), 16);
    let b = parseInt(hex.substring(4, 6), 16);
    if (isGray) {
        // let v = (0.58 * (r / 255)) + (0.17 * (g / 255)) + (0.8 * (b / 255));
        let v = (0.58 * r) + (0.17 * g) + (0.8 * b);
        thecol = 'rgba(' + v + ',' + v + ',' + v + ',' + visibility + ')';
    } else {
        thecol = 'rgba(' + r + ',' + g + ',' + b + ',' + visibility + ')';
    }
    return thecol;
};

